    //     background-size: cover;
    // }

    .intro-content {
        h1 {
            // font-weight: 400;
            line-height       : 55px;
            margin-bottom     : 30px;
            // strong {
            //     font-weight: 500;
            // }
        }

        p {
            font-size    : 20px;
            margin-bottom: 50px;
        }
    }

    .shape {
        position  : absolute;
        bottom    : 0;
