.intro-form-exchange {
    padding      : 30px;
    box-shadow   : 0px 20px 25px rgba(22, 28, 45, 0.05);
    border-radius: 5px;
    background   : #fff;
    z-index      : 1;

    .nice-select {
        border-top-left-radius   : 5px !important;
        border-bottom-left-radius: 5px !important;
    }

    .btn {
        padding    : 10px 15px;
        font-weight: 600;
        position   : relative;
        font-size  : 16px;

        i {
            font-size  : 22px;
            font-weight: bold;
            position   : absolute;
            right      : 15px;
        }
    }
}

.shape-container {
    position : absolute;
    left     : 0;
    right    : 0;
    width    : 100%;
    // height: 150px;
    z-index  : 2;
    transform: translateZ(0);
    overflow : hidden;
}

.shape-container[data-shape-position="bottom"] {
    top          : auto;
    bottom       : 0;
    margin-bottom: -6px;
}

.shape-container svg {
    fill          : #FFF;
    pointer-events: none;
    vertical-align: baseline;
}