.intro-video-play {
    background-color: $white;
    background-image: url('./../images/background/2.jpg');
    background-size: cover;
    background-position: 0 0;
    min-height: 465px;
    // max-width: 800px;
    margin: 0 auto;
    position: relative;
    border-radius: 5px;
    z-index: 1;
    margin-bottom: 30px;
    box-shadow: 0 1.5rem 4rem rgba(22, 28, 45, 0.1);
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: $primary;
        opacity: 0.45;
        border-radius: 5px;
        z-index: -1;
    }
}

.play-btn {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    a {
        background: $white;
        width: 75px;
        height: 75px;
        border-radius: 100px;
        padding: 27px 0;
        text-align: center;
        cursor: pointer;
        i {
            color: $primary;
        }
    }
}

