// stylelint-disable-next-line scss/dollar-variable-default
$primary   : #6e00ff;
$secondary : #EFF2F7 !default;
$success   : #36B37E !default;
$info      : #00B8D9 !default;
$warning   : #FFAB00 !default;
$danger    : #FF5630 !default;
$light     : #FAFBFE !default;
$dark      : #273444;

// Body
$body-bg    : #fff;
$body-color :#8492A6;




// Typography
$font-family-base : 'Nunito',
sans-serif;
$headings-font-weight : 600;
$font-size-base       : 0.9375rem !default;
$font-weight-base       : 400 !default;
// $line-height-base    : 1.6;
$h1-font-size           : 2.25rem;
$h2-font-size           : 1.875rem;
$h3-font-size           : 1.5rem;
$h4-font-size           : 1.125rem;
$h5-font-size           : 1rem;
$h6-font-size           : 0.875rem;




$text-muted: #AEAED5;

$heading-primary-size : 1.8rem;
$sub-heading-size     : 1.6rem !default;
$grid-gutter-width    : 30px;
$dropdown-lik-color   : $body-color;
$border-color         : #EFF2F7;
$headings-color       :$dark !default;
$table-striped-bg     : $body-bg;
$border-radius        : 8px !default;
$input-btn-focus-width: 0rem !default;

// custom
$radius:$border-radius;
$shadow : 0 0.75rem 1.5rem rgba(18, 38, 63, 0.03);